import * as React from 'react';
import { useState, useEffect } from 'react';
import logo from '../public/ecollm_logo.jpg';
import { createRoot } from 'react-dom/client';
import { Wifi } from 'react-bootstrap-icons';
import { log4TSProvider } from './config/ConfigLog4j';
const log = log4TSProvider.getLogger("header.ts");
export function Header() {
    const [connectionStatus, setConnectionStatus] = useState('disconnected');
    log.debug("Setting up header.");
    useEffect(() => {
        chrome.storage.local.get(['connectionStatus'], (result) => {
            if (result.connectionStatus) {
                setConnectionStatus(result.connectionStatus);
                log.debug("update header with connection status: " + result.connectionStatus);
            }
        });
        const listener = (changes, areaName) => {
            if (areaName === 'local' && changes['connectionStatus']) {
                setConnectionStatus(changes['connectionStatus'].newValue);
                log.debug("update header with connection status: " + changes['connectionStatus'].newValue);
            }
        };
        chrome.storage.onChanged.addListener(listener);
        return () => {
            chrome.storage.onChanged.removeListener(listener);
        };
    }, []);
    return (React.createElement("div", { className: "d-flex align-items-center p-2", style: { width: '100%', backgroundColor: 'white' } },
        React.createElement("img", { src: logo, alt: "EcoLLM logo", style: { height: '60px', width: 'auto', marginRight: '15px' } }),
        React.createElement("div", { className: "d-flex flex-column justify-content-center" },
            React.createElement("h2", { className: "m-0", style: { fontSize: '1.2rem', fontWeight: 'bold', lineHeight: '1.2' } },
                "1",
                React.createElement("sup", null, "er"),
                " Compagnon GreenAI"),
            React.createElement("p", { className: "m-0 text-muted", style: { fontSize: '0.9rem' } },
                "\u00C9valuez l'impact CO",
                React.createElement("sup", null, "2"),
                " de votre IA")),
        React.createElement("div", { className: "ms-auto" },
            React.createElement(Wifi, { color: connectionStatus === 'connected' ? 'green' : 'grey', size: 20 }))));
}
export function renderHeader() {
    const domNode = document.getElementById('header');
    const root = createRoot(domNode);
    root.render(React.createElement(Header, null));
}
